import React, { useRef } from 'react';
import { StaticImage } from 'gatsby-plugin-image';

// utils

// assets
const LOGO_PATH = '../images/logo.png';

// actions

// components

// self-defined-components

const AppHeader = () => {
  const headerRef = useRef(null);

  const handleClick = (anchor) => {
    const element = document.querySelector(`#${anchor}`);

    if (element) {
      const headerHeight = headerRef.current.clientHeight;
      const offsetTop = element.offsetTop;
      window.scrollTo({ top: offsetTop - headerHeight, behavior: 'smooth' });
    }
  };

  return (
    <div
      ref={headerRef}
      className="sticky top-0 z-10"
    >
      <div
        className="px-5 md:px-10 lg:px-32 select-none flex justify-between items-center bg-[color:#333333]"
      >
        <div className="w-16 h-16">
          <StaticImage
            src={LOGO_PATH}
            alt="logo"
          />
        </div>
        <div className="h-16">
          <ul className="text-white h-full flex">
            <li className="flex items-center transition duration-200 ease-in-out hover:bg-[color:#2b9bfb]">
              <button
                className="px-4 w-full h-full"
                onClick={() => handleClick('about-me')}
              >
                About Me
              </button>
            </li>
            <li className="flex items-center transition duration-200 ease-in-out hover:bg-[color:#2b9bfb]">
              <button
                className="px-4 w-full h-full"
                onClick={() => handleClick('recent')}
              >
                Recent
              </button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default AppHeader;
