import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Lightbox from 'react-image-lightbox';

import 'react-image-lightbox/style.css';

// utils

// assets

// actions

// components

// self-defined-components

const Album = ({ coverTitle, title, description, cover, images }) => {
  const [photoIndex, setPhotoIndex] = useState(0);
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className="m-1.5">
      <button
        className="w-full group relative flex items-center overflow-hidden"
        onClick={() => setIsOpen(true)}
      >
        <GatsbyImage
          className="w-full filter brightness-70 transition duration-200 ease-in-out group-hover:brightness-40"
          image={getImage(cover)}
          alt={title}
        />
        <div className="absolute px-7 py-6 w-full text-3xl md:text-xl text-white text-center font-semibold select-none">{coverTitle}</div>
      </button>

      {isOpen && (
        <Lightbox
          imageCaption={
            <div className="text-white text-center px-10 py-5">
              {title && <div className="text-2xl font-bold leading-none">{title}</div>}
              {description && <div className="font-ibm mt-2.5 leading-none break-words">{description}</div>}
            </div>
          }
          mainSrc={images[photoIndex]}
          nextSrc={images[(photoIndex + 1) % images.length]}
          prevSrc={images[(photoIndex + images.length - 1) % images.length]}
          enableZoom={false}
          wrapperClassName="customLightboxModal"
          onCloseRequest={() => setIsOpen(false)}
          onMovePrevRequest={() =>
            setPhotoIndex((photoIndex + images.length - 1) % images.length)
          }
          onMoveNextRequest={() =>
            setPhotoIndex((photoIndex + 1) % images.length)
          }
        />
      )}
    </div>
  );
};

Album.propTypes = {
  coverTitle: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.string,
  cover: PropTypes.shape({}).isRequired,
  images: PropTypes.arrayOf(PropTypes.string).isRequired,
};

Album.defaultProps = {
  coverTitle: '',
  title: '',
  description: '',
};

export default Album;
