import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';

// utils

// assets

// actions

// components

// self-defined-components

const Recent = () => {
  const {
    allMarkdownRemark: {
      edges
    },
  } = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        sort: {
          order: DESC,
          fields: [frontmatter___date]
        },
        filter: {
          fileAbsolutePath: { regex: "/contents\/participated_events/" }
        },
        limit: 4
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              description
              coverImage {
                id
                childImageSharp {
                  gatsbyImageData(
                    layout: FULL_WIDTH
                    aspectRatio: 1.78
                    transformOptions: { cropFocus: CENTER }
                  )
                }
              }
              url
            }
          }
        }
      }
    }
  `);

  return (
    <div
      id="recent"
      className="px-5 md:px-10 lg:px-32 pt-20"
    >
      <h2 className="mb-6 text-4xl text-[color:#555555] text-center">Recent</h2>

      {edges.map(({
        node: {
          id,
          frontmatter: {
            title,
            description,
            coverImage,
            url,
          },
        },
      }) => (
        <div key={id} className="mt-9 grid grid-cols-12 md:gap-12">
          <div className="col-span-12 md:col-span-7 self-auto md:self-center order-2 md:order-1">
            <h2
              className="font-ibm text-lg text-[color:#222222] mt-2.5 md:mt-0"
              dangerouslySetInnerHTML={{ __html: `“${title.replace(/\n/g, "<br />")}”` }}
            />
            <div className="text-sm italic text-[color:#555555]">{description}</div>
            {url && (
              <a
                className="mt-4 font-semibold leading-none text-[color:#50555c] border-2 border-[color:#50555c] rounded inline-block px-5 py-2.5 transition duration-200 ease-in-out hover:opacity-80"
                href={url}
                alt={url}
                target="_blank"
                rel="noreferrer noopener"
              >
                more...
              </a>
            )}
          </div>
          <div className="col-span-12 md:col-span-5 order-1 md:order-2">
            {url ? (
              <a href={url} alt={url} target="_blank" rel="noreferrer noopener">
                <GatsbyImage
                  className="transition duration-200 ease-in-out hover:opacity-70"
                  image={getImage(coverImage)}
                  alt={title}
                />
              </a>
            ) : (
              <GatsbyImage
                className="transition duration-200 ease-in-out hover:opacity-70"
                image={getImage(coverImage)}
                alt={title}
              />
            )}
          </div>
        </div>
      ))}
    </div>
  );
};

export default Recent;
