import React, { useEffect, useState, useRef } from 'react';

// utils

// assets
import arrowToTop from '@/images/arrow-to-top.svg';

// actions

// components

// self-defined-components

const BackToTop = () => {
  const observer = useRef(null);
  const watchRef = useRef(null);
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    observer.current = new IntersectionObserver(entries => {
      if (entries[0].boundingClientRect.y < 0) {
        setVisible(true);
      } else {
        setVisible(false);
      }
    });

    const element = watchRef.current;
    observer.current.observe(element);

    return function cleanup() {
      observer.current.unobserve(element);
    };
  });

  return (
    <>
      <div
        ref={watchRef}
        id="pixel-to-watch"
        className="absolute w-px h-px top-[400px]"
      />

      <button
        className={`z-10 fixed right-5 bottom-5 flex justify-center items-center w-10 h-10 rounded-full bg-black transition duration-200 ease-in-out ${visible ? 'opacity-40' : 'opacity-0'}`}
        onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
      >
        <img className="h-4" src={arrowToTop} alt="back to top" />
      </button>
    </>
  );
};

export default BackToTop;
