import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

// utils

// assets

// actions

// components
import Album from '@/components/Album';

// self-defined-components

const ParticipatedEvent = () => {
  const {
    allFile: {
      edges: fileEdges,
    },
    allMarkdownRemark: {
      edges: remarkEdges
    },
  } = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          sourceInstanceName: { eq: "images" },
          relativeDirectory: { regex: "/participated_events/" }
        }
        sort: {
          fields: [relativeDirectory, name],
          order: ASC
        }
      ) {
        edges {
          node {
            relativeDirectory
            sourceInstanceName
            childImageSharp {
              gatsbyImageData(
                layout: FULL_WIDTH
                aspectRatio: 1.5
                transformOptions: { cropFocus: CENTER }
              )
            }
            publicURL
          }
        }
      }
      allMarkdownRemark(
        sort: {
          order: DESC,
          fields: frontmatter___date
        },
        filter: {
          fileAbsolutePath: { regex: "/contents\/participated_events/" }
        }
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              coverTitle
              coverImage {
                childImageSharp {
                  gatsbyImageData(
                    layout: FULL_WIDTH
                    aspectRatio: 1.5
                    transformOptions: { cropFocus: CENTER }
                  )
                }
              }
              description
              albumPath
            }
          }
        }
      }
    }
  `);

  return (
    <div className="px-5 md:px-10 lg:px-32 pt-20">
      <h2 className="mb-6 text-4xl text-[color:#555555] text-center break-all">Conferences/Meetings/topics that participated in the past</h2>

      <div className="grid grid-cols-1 md:grid-cols-4">
        {remarkEdges.map(({
          node: {
            id,
            frontmatter: {
              title,
              coverTitle,
              coverImage,
              description,
              albumPath,
            },
          },
        }) => {
          const imageNodes = fileEdges.filter((ele) => {
            return `${ele.node.sourceInstanceName}/${ele.node.relativeDirectory}` === albumPath;
          });

          if (imageNodes.length > 0) {
            const cover = coverImage ? coverImage : imageNodes[0].node;
            const images = [];

            imageNodes.forEach(ele => images.push(ele.node.publicURL));

            return (
              <div key={id} className="col-span-4 md:col-span-1">
                <Album
                  coverTitle={coverTitle}
                  title={title}
                  description={description}
                  cover={cover}
                  images={images}
                />
              </div>
            );
          }

          return null;
        })}
      </div>
    </div>
  );
};

export default ParticipatedEvent;
