import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

// utils

// assets

// actions

// components

// self-defined-components

const FBLikeButton = ({ url, width, layout, action, size, includeShare }) => {
  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://connect.facebook.net/en_US/sdk.js#xfbml=1&version=v6.0';
    document.body.appendChild(script);
  });

  return (
    <div
      className="fb-like"
      data-href={url}
      data-width={width}
      data-layout={layout}
      data-action={action}
      data-size={size}
      data-share={includeShare}
    />
  );
};

FBLikeButton.propTypes = {
  url: PropTypes.string.isRequired,
  width: PropTypes.number,
  layout: PropTypes.oneOf(['standard', 'box_count', 'button_count', 'button']),
  action: PropTypes.oneOf(['like', 'recommand']),
  size: PropTypes.oneOf(['small', 'large']),
  includeShare: PropTypes.bool,
};
FBLikeButton.defaultProps = {
  width: 100,
  layout: 'button_count',
  action: 'like',
  size: 'small',
  includeShare: false,
};

export default FBLikeButton;
