import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faTwitter,
  faLinkedinIn,
  faMediumM,
} from '@fortawesome/free-brands-svg-icons'

// utils

// assets

// actions

// components

// self-defined-components

const RenderSocialLink = ({ name, url }) => {
  switch(name) {
    case 'twitter':
      return (
        <a
          className="mx-3 rounded-full text-white inline-flex justify-center items-center transition duration-200 ease-in-out hover:opacity-70"
          href={url}
          target="_blank"
          rel="noreferrer noopener"
          aria-label="Twitter"
          style={{ width: 50, height: 50, fontSize: '28px', backgroundColor: '#1da1f2' }}
        >
          <FontAwesomeIcon icon={faTwitter} />
        </a>
      );
    case 'linkedin':
      return (
        <a
          className="mx-3 rounded-full text-white inline-flex justify-center items-center transition duration-200 ease-in-out hover:opacity-70"
          href={url}
          target="_blank"
          rel="noreferrer noopener"
          aria-label="LinkedIn"
          style={{ width: 50, height: 50, fontSize: '28px', backgroundColor: '#0077b5' }}
        >
          <FontAwesomeIcon icon={faLinkedinIn} />
        </a>
      );
    case 'medium':
      return (
        <a
          className="mx-3 rounded-full text-white inline-flex justify-center items-center transition duration-200 ease-in-out hover:opacity-70"
          href={url}
          target="_blank"
          rel="noreferrer noopener"
          aria-label="Medium"
          style={{ width: 50, height: 50, fontSize: '28px', backgroundColor: '#3a3d4a' }}
        >
          <FontAwesomeIcon icon={faMediumM} />
        </a>
      );
    default:
      return null;
  }
};

RenderSocialLink.propTypes = {
  name: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
};

const FindMe = () => {
  const {
    site: {
      siteMetadata: {
        socialNetworks,
      },
    },
  } = useStaticQuery(graphql`
    query  {
      site {
        siteMetadata {
          socialNetworks {
            name
            url
          }
        }
      }
    }
  `);

  return (
    <div className="px-5 md:px-10 lg:px-32 pt-20 text-center select-none">
      <h2 className="mb-6 text-4xl text-[color:#555555]">Find Me</h2>

      {socialNetworks.map((data) => (
        <RenderSocialLink key={data.name} {...data} />
      ))}
    </div>
  );
};

export default FindMe;
