import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

// utils

// assets

// actions

// components
import FBLikeButton from '@/components/FacebookLikeButton';
import TwitterShareButton from './TwitterShareButton.js';
import LinkedInShareButton from './LinkedInShareButton.js';

// self-defined-components

const AppFooter = () => {
  const {
    site: {
      siteMetadata: {
        title,
        author,
        siteURL,
      },
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          author
          siteURL
        }
      }
    }
  `);

  return (
    <div className="px-5 md:px-10 lg:px-32 py-20">
      <div className="grid grid-cols-2">
        <div className="col-span-2 md:col-auto flex justify-center md:justify-start">
          <FBLikeButton url={siteURL} />
          <TwitterShareButton className="ml-1.5" text={title} />
          <LinkedInShareButton className="ml-3.5 inline-flex" url={siteURL} />
        </div>
        <div className="col-span-2 md:col-auto">
          <div className="text-sm text-[color:#50555c] mt-2.5 md:mt-0 text-center md:text-right">
            &copy; {new Date().getFullYear()} {author}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AppFooter;
