import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';

// utils

// assets

// actions

// components

// self-defined-components

const Blog = () => {
  const {
    allMarkdownRemark: {
      edges
    },
  } = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        sort: {
          order: DESC,
          fields: [frontmatter___date]
        },
        filter: {
          fileAbsolutePath: { regex: "/contents\/blogs/" }
        },
        limit: 6
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              date(formatString: "MMM DD, YYYY")
              coverImage {
                id
                childImageSharp {
                  gatsbyImageData(
                    layout: FULL_WIDTH
                    aspectRatio: 1.5
                    transformOptions: { cropFocus: CENTER }
                  )
                }
              }
              url
            }
          }
        }
      }
    }
  `);

  return (
    <div className="px-5 md:px-10 lg:px-32 pt-20">
      <h2 className="mb-6 text-4xl text-[color:#555555] text-center">Blog</h2>

      <div className="grid grid-cols-3 gap-x-4 gap-y-10">
        {edges.map(({
          node: {
            id,
            frontmatter: {
              title,
              date,
              coverImage,
              url,
            },
          },
        }) => (
          <div key={id} className="col-span-3 md:col-auto">
            <a
            className="text-center"
              href={url}
              alt={url}
              target="_blank"
              rel="noreferrer noopener"
            >
              <GatsbyImage
                className="transition duration-200 ease-in-out hover:opacity-70"
                image={getImage(coverImage)}
                alt={title}
              />
              <div className="font-ibm text-lg text-[color:#222222] mt-2.5">{title}</div>
              <div className="text-[color:#555555] mt-1.5">{date}</div>
            </a>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Blog;
