const TwitterShareButton = ({ className, text }) => (
  <div className={className}>
    <iframe
      title="Twitter"
      allowtransparency="true"
      frameBorder="0"
      scrolling="no"
      src={`//platform.twitter.com/widgets/tweet_button.html?text=${text}`}
      style={{ width: 73, height: 20 }}
    />
  </div>
);

TwitterShareButton.propTypes = {
  className: PropTypes.string,
  text: PropTypes.string,
};
TwitterShareButton.defaultProps = {
  className: null,
  text: '',
};

export default TwitterShareButton;
