import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

// utils

// assets
import bannerImg from '@/images/banner.jpg';

// actions

// components

// self-defined-components

const Banner = () => {
  const {
    site: {
      siteMetadata: {
        title,
      },
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  return (
    <div
      id="banner"
      className="px-8 py-20 bg-no-repeat bg-cover bg-center select-none text-center text-white"
      style={{
        backgroundImage: `url(${bannerImg})`
      }}
    >
      <h1 className="font-semibold text-5xl">{title.toUpperCase()}</h1>
      <div className="font-ibm text-lg mt-6">“ Data Transform The Experience, And The Experience Converges The Insights ”</div>
    </div>
  );
};

export default Banner;
